<template>
  <div class="box"
       style="position:relative">
    <v-btn rounded
           v-if="$route.name=='allClientMsg'"
           style="position: absolute;top:-53px;right:60px;"
           color="primary"
           @click="goTranslate"
           v-btnAuth="['enter-translate-page']"
           dark>{{$t('label_translation')}}</v-btn>
    <mrc_ess_table v-if="$route.name=='allClientMsg'"
                   :headers='headers'
                   :search='search'
                   :hideAddBtn='!hasBtn("add-label-internationalization-info")'
                   pageName='addAllClientMsg'
                   :slotKey="['status']"
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :items='desserts'>
      <template v-slot:top>
        <v-card-title style="height:60px;position:relative;">
          <!-- <mrc_ess_searchInput :isShow='true'
                               @change="searchChange"
                               style="position:absolute;right:10px;top:18px;"></mrc_ess_searchInput> -->
        </v-card-title>
      </template>

      <!-- 状态-->
      <template v-slot:item.status='{item}'>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.status===1">{{$t('label_To_enable_the')}}</v-btn>
        <v-btn rounded
               color="#999999"
               dark
               x-small
               v-if="item.status===0">{{$t('label_disable')}}</v-btn>
      </template>
      <template #item.language_master_content='{item}'>
        {{INT(item.language_master_content)}}
      </template>
      <!-- 按钮 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon v-btnAuth="['view-label-internationalization-info',
                                'edit-label-internationalization-info',
                                'delete-label-internationalization-info']">mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template> 

<script>
import { gethintList, gethintDetail, deletehint, addehint } from '@/service/api/internationalization.js'

export default {
  components: {

  },
  props: {},
  data () {
    return {
      loading: false,
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      desserts: [],
      search: ''
    };
  },
  created () { },
  async mounted () {
    // console.log(this.$route.meta.title);
    this.$route.meta.title = '国际化基础数据' + (this.$store.state.client_type ? '->' : '') + this.$store.state.client_type
    await this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    searchChange (v) {
      this.search = v
    },
    goTranslate () {
      this.$router.push({
        name: 'detaillabelInttranslation'
      })
    },
    rowgodetail (item) {
      // console.log(item);
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$store.commit('set_uuid2', '')  //类型
      this.$store.commit('set_uuid', item.uuid)  //
      this.$store.commit('set_subPage_name', item.language_master_key)  //
      this.$router.push({
        name: 'detailAllClientMsg',
        params: { title: item.language_master_key },
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.rowgodetail(item)
          break
        case 1:
          this.$store.commit('set_iconstatus', false)  //行内进图标显示
          this.$store.commit('set_uuid2', '')  //类型
          this.$store.commit('set_uuid', item.uuid)  //
          this.$store.commit('set_subPage_name', item.language_master_key)  //
          this.$router.push({
            name: 'detailAllClientMsg',
            params: { title: item.language_master_key }
          })
          break
        case 2:
          this.$myConfirm({
            content: '确认要删除吗？'
          }).then(res => {
            deletehint({
              uuid: item.uuid
            }).then(res => {
              if (res.code == 200) {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                this.getlist()
              }
            })
          }).catch(err => { })
          break
      }
    },
    goadd () {
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$store.commit('set_uuid', '')  //
      this.$store.commit('set_uuid2', '')  //类型
      this.$store.commit('set_subPage_name', '')  //
      this.$router.push({
        name: 'addAllClientMsg'
      })
    },
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const client_type = this.$store.state.client_type
      const version = this.$store.state.version
      const res = await gethintList({  //换个类型
        client_type, version,
        cpage: this.options.cpage,
        pagesize: this.options.pagesize
      })
      if (res.code === '200') {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
  },
  computed: {
    headers () {
      return [
        {
          text: 'Key',
          align: 'start',
          sortable: false,
          value: 'language_master_key',
        },
        {
          text: this.$t('label_The_name_of_the_module'),
          align: 'start',
          sortable: false,
          value: 'language_master_module_uuid',
        },
        {
          text: this.$t('label_The_name_of_the_function'),
          align: 'start',
          sortable: false,
          value: 'language_master_function_uuid',
        },
        {
          text: this.$t('label_content'),
          align: 'start',
          sortable: false,
          value: 'language_master_content',
        },
        {
          text: this.$t('label_version'),
          align: 'start',
          sortable: false,
          value: 'version',
        },
        {
          text: this.$t('label_type'),
          align: 'start',
          sortable: false,
          value: 'language_data_type',
        },
        {
          text: this.$t('label_state'),
          align: 'center',
          sortable: false,
          value: 'status',
        },
        {
          text: '',
          value: 'actions',
          sortable: false,
          align: 'center',
        }
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View'), show: this.hasBtn('view-label-internationalization-info') },
        { name: this.$t('label_Edit'), show: this.hasBtn('edit-label-internationalization-info') },
        { name: this.$t('label_delete'), show: this.hasBtn('delete-label-internationalization-info') },
      ]
    }
  },
  watch: {
    $route (v) {
      if (v.name == 'allClientMsg') {
        this.getlist()
      }
    }
  },
}
</script>

<style scoped>
.box /deep/ .btnGroup {
  position: relative;
}
.box /deep/ .addBtn {
  color: #fff;
  position: absolute;
  top: -55px;
  right: 10px;
}
</style>
